import React from 'react';
import useFetch from 'use-http';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Props } from '../index';
// import env from "react-dotenv";

import Form from './Form';

export interface iApc {
  status: boolean;
  message: string;
  noM_ASOC: string;
  descR_CORTA_RELA: string;
  montO_ORIGINAL: string;
  nuM_PAGOS: string;
  descR_FORMA_PAGO: string;
  importE_PAGO: string;
  feC_ULTIMO_PAGO: string;
  montO_ULTIMO_PAGO: string;
  descrR_OBS_CORTA: string;
  saldO_ACTUAL: string; 
  nuM_DIAS_ATRASO: string;
  historia: string;
  nuM_REFER: string;
  score: string;
  pi: string;
  exclusion: string; 
  montoAplicado: number;
  montoARecibir: number;
}

function Aproach(props: Props) {

  // const usuarioApc = env.USUARIO_APC;
  // const claveApc = env.CLAVE_APC;
  // const productoApc = env.PRODUCTO_APC;

  const usuarioApc = "WSACSORAT001";
  const claveApc = "7DNhn2E5$ErHkJ^";
  const productoApc = "1";

  let ID = ""
  let IDMongo = window.localStorage.getItem('IDMongo') || '';
  if (IDMongo) {
    ID = JSON.parse(IDMongo).ID
  }

  const { application: { borrower: { id } } } = props;
  // const { application: { borrower: { tipoCliente } } } = props;
  // Tipo del Cliente
  // 1 = Cdula
  // 2 = RUC
  // 3 = Pasaporte

  const tipoCliente = "1"

  const { data: refApc, loading } = useFetch<iApc[]>(
    "/APC",
    { 
      method: "POST",
      body: JSON.stringify({ "usuarioApc": usuarioApc, "claveApc": claveApc, "id": id, "tipoCliente": tipoCliente, "productoApc": productoApc, "idMongo": ID })
    },
    []
  );
  
  if (loading || !refApc) {
    return (
      <div style={{
        display:'flex',
        height:'100%',
        justifyContent: 'center',
        alignItems: 'center'
      }}>
        <CircularProgress />
      </div>
    );
  }

  return <Form {...props} refApc={refApc} />;
};

export default Aproach;