import React from 'react';
import Grid from '@material-ui/core/Grid';
import FormLabel from '@material-ui/core/FormLabel';
import { SvgIconProps } from '@material-ui/core/SvgIcon';
import { makeStyles } from '@material-ui/core/styles';

import OptionCard from '../Cards/OptionCard';

const useStyles = makeStyles({
  label: {
    marginBottom:24
  }
});

interface RadioOption<U> {
  label: string;
  Icon: React.ElementType<SvgIconProps>;
  value: U;
};

interface Props<T> {
  label: string;
  onClick: (value: T) => void;
  options: RadioOption<T>[];
  value?: T;
};

function CardRadioButtons<T>(props: Props<T>) {
  const classes = useStyles();
  return (
    <Grid container>
      <Grid item xs={12} classes={{item: classes.label}}>
        <FormLabel>{props.label}</FormLabel>
      </Grid>
      <Grid item xs={12} container spacing={2}>
        {props.options.length && props.options.map((option, idx) => (
          <Grid key={idx} item xs={6} sm={2}>
            <OptionCard 
              title={option.label}
              onClick={() => props.onClick(option.value)}
              Icon={option.Icon}
              selected={option.value === props.value}
            />
          </Grid>
        ))}
      </Grid>
    </Grid>
  );
}

export default CardRadioButtons;