import React from 'react';
import useFetch from 'use-http';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Props } from '../index';

import Form from './Form';

export interface LaboralSector {
  id: number;
  sector: string;
  id_sector: number,
  name: string;
}

export interface TermLoan {
  id: number;
  name: number;
}

export interface LaboralEntity {
  ruta: string,
  id_sector: number,
  id_profesion: number,
  discount_capacity: number;
  discount_capacity_mortgage: number;
  debt_capacity: number;
  debt_capacity_mortgage: number;
  plazo_max: number;
  tasa: number;
  comision: number;
  salario_min: number;
  tasa_servicio: number;
  seg_vida: number;
  feci: number;
  itbms: number;
  notaria: number;
  mount_min: number;
  mount_max: number;
  factor: number;
  letraRetenida: boolean;
  gastoLegal: number;
  timbres: number;
  servicioDescto: number;
}

export interface Loan {
  bank: string;
  loan: number;
  term: number;
  paysYear: number;
  monthlyFee: number;
  cashOnHand: number;
}


export interface Subgrupo_inst {
  id: number;
  tasa_mes_menor: number;
  tasa_mes_mayor: number;
  anios: number;
  plazo_maximo: number;
}

function Terms(props: Props) {
  const {
    data: sectorResponse,
    loading: sectorLoading,
  } = useFetch<LaboralSector[]>('/laboral_sector', { method: 'GET' }, []);

  const { data: termsLoan, loading: loadingTermL } = useFetch<TermLoan[]>(
    "/terms_loan",
    { method: "GET" },
    []
  );

  const { data: laboralEntitys, loading } = useFetch<LaboralEntity[]>(
    "/laboral_sector_entity_f",
    { method: "GET" },
    []
  );

  const { data: subgrupo_inst, loading: loadingSubgrupo } = useFetch<Subgrupo_inst[]>(
    "/subgrupo_institution",
    { method: "GET" },
    []
  );  

  if (loading || !laboralEntitys || loadingTermL || !termsLoan
    || sectorLoading || !sectorResponse || loadingSubgrupo || !subgrupo_inst) {
    return (
      <div style={{
        display:'flex',
        height:'100%',
        justifyContent: 'center',
        alignItems: 'center'
      }}>
        <CircularProgress />
      </div>
    );
  }

  return <Form 
    {...props} 
    laboralEntitys={laboralEntitys}
    termsLoan={termsLoan}
    sectors={sectorResponse}
    subgrupo_inst={subgrupo_inst}
  />
};

export default Terms;