import React from 'react';
import { Props } from '../index';
import CircularProgress from '@material-ui/core/CircularProgress';

import Form from './Form';

export interface iApc {
  status: boolean;
  message: string;
  noM_ASOC: string;
  descR_CORTA_RELA: string;
  montO_ORIGINAL: string;
  nuM_PAGOS: string;
  descR_FORMA_PAGO: string;
  importE_PAGO: string;
  feC_ULTIMO_PAGO: string;
  montO_ULTIMO_PAGO: string;
  descrR_OBS_CORTA: string;
  saldO_ACTUAL: string; 
  nuM_DIAS_ATRASO: string;
  historia: string;
  nuM_REFER: string;
  score: string;
  pi: string;
  exclusion: string; 
  montoAplicado: number;
  montoARecibir: number;
}

function Aproach(props: Props) {

  const { application: { borrower } } = props;

  const id = borrower.id || ""
  let refApc: iApc[] = []
  const data = window.localStorage.getItem('APC-'+id)
  if(data){
      console.log(data)
      refApc = JSON.parse(data).data
  }

  if (!refApc) {
    return (
      <div style={{
        display:'flex',
        height:'100%',
        justifyContent: 'center',
        alignItems: 'center'
      }}>
        <CircularProgress />
      </div>
    );
  }

  return <Form {...props} refApc={refApc} />;
};

export default Aproach;