import React from 'react';
import { useHistory } from "react-router-dom";
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

import { makeStyles, Theme } from '@material-ui/core/styles';

import Faqs from './Faqs';
import Allies from './Allies';
import Steps from './Steps';
// import Slider from './Slider';

import { ProductCard } from '../../components/Cards';

import PRODUCTS from '../../lib/products';
import STRINGS from '../../strings';
 
const textShadow = '0px 4px 10px rgba(0, 0, 0, 0.25)';

const useStyles = makeStyles((theme: Theme) => ({
  container: {
    paddingTop: 72,
    paddingBottom: 72
  },
  buttonContainer: {
    paddingBottom: 72 * 2,
    textAlign: 'center'
  },
  heroBtn: {
    backgroundColor: theme.palette.success.main,
    color: 'white'
  },
  background: {
    backgroundColor: theme.palette.primary.light
  },
  title: {
    color: theme.palette.primary.dark,
    '& span': {
      letterSpacing: '-0.05em',
      fontWeight: theme.typography.fontWeightBold
    },
    textShadow: textShadow,
    textAlign: 'center'
  },
  stepsSubtitle: {
    fontWeight: theme.typography.fontWeightRegular
  },
  alliesTitle: {
    marginBottom: 16
  },
  products: {
    marginBottom: 48
  },
  subtitle: {
    letterSpacing: '-0.05em',
    fontWeight: theme.typography.fontWeightRegular,
    textAlign: 'center',
    textShadow: textShadow,
    '& span': {
      fontWeight: theme.typography.fontWeightBold,
      color: theme.palette.secondary.main
    }
  },
  subtitle2: {
    fontWeight: theme.typography.fontWeightRegular,
    textAlign: 'center',
    textShadow: textShadow,
    '& span': {
      fontWeight: theme.typography.fontWeightBold,
      color: theme.palette.secondary.main
    }
  },
  subtitle3: {
    letterSpacing: '-0.08em',
    fontWeight: theme.typography.fontWeightRegular,
    textAlign: 'center',
    textShadow: textShadow,
    '& span': {
      fontWeight: theme.typography.fontWeightBold,
      color: theme.palette.secondary.main
    }
  },
}));

function Home() {
  const classes = useStyles();
  let history = useHistory();

  function onProductClick(path: string) {
    history.push(path);
  }

  function onButtonClick() {
    const target = document.getElementById('products');

    if (target) {
      target.scrollIntoView({ behavior: 'smooth', block: 'start' });
    }
  }

  return (
    <>
      {/* <Slider /> */}
      <div className={classes.background} id='products'>
        <Container classes={{ root: classes.container }}>
          <Typography classes={{root:classes.title}} color='inherit' variant='h3' paragraph>{STRINGS.HOME.CAROUSEL.SLIDES[0].TITLE}</Typography>
          <Typography classes={{root:classes.subtitle}} color='inherit' variant='h4' paragraph>{STRINGS.HOME.CAROUSEL.SLIDES[0].SUBTITLE}</Typography>
          <Typography classes={{root:classes.subtitle2}} color='inherit' variant='h4' paragraph>{STRINGS.HOME.CAROUSEL.SLIDES[0].SUBTITLE2}</Typography>
          <Typography classes={{root:classes.subtitle3}} variant='h4' >{STRINGS.HOME.WALLET.TITLE}</Typography>
          <Grid container spacing={2} classes={{container: classes.products}}>
            {Object.values(PRODUCTS).map((product, idx) => (
              <Grid key={idx} item xs={6} sm={4} md={4} lg={2}>
                <ProductCard 
                  key={product.id}
                  disabled={product.disabled} 
                  Icon={product.icon}
                  title={product.name}
                  onClick={() => onProductClick(product.path)} 
                />
              </Grid>
            ))}
          </Grid>
          <Typography variant='body1' align='center' classes={{root: classes.alliesTitle}}>{STRINGS.HOME.ALLIES.TITLE}</Typography>
          <Allies />
        </Container>
      </div>
      <Container classes={{ root: classes.container }} id='steps'>
        <Grid container spacing={2} justify='center'>
          <Grid item xs={12} sm={8}>
            <Typography variant='h4' align='center' gutterBottom>{STRINGS.HOME.STEPS.TITLE}</Typography>
            <Typography variant='h6' align='center' color='textSecondary' classes={{ root: classes.stepsSubtitle }}>{STRINGS.HOME.STEPS.SUBTITLE}</Typography>
          </Grid>
        </Grid>
      </Container>
      <Steps  />
      <Container classes={{ root: classes.buttonContainer }}>
        <Button onClick={onButtonClick} variant='contained' color='secondary' size='large' disableElevation>{STRINGS.HOME.STEPS.CTA}</Button>
      </Container>
      <div className={classes.background}>
        <Container classes={{ root: classes.container }} id='faqs'>
          <Typography align='center' variant='h4' classes={{root: classes.title}}>{STRINGS.HOME.FAQ.TITLE}</Typography>
          <Faqs />
        </Container>
      </div>
    </>
  );
};

export default Home;