import React, { useEffect } from 'react';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';

import { TextFieldPhone } from '../../../../../components/Fields';
import STRINGS from '../../../../../strings';
import { Props as ApplicationProps } from '../index';
import { MRG_BOTT } from '../../../../Quotation/PersonalLoan/types';
import { ICounty, IProvince, IDistrict } from './index';
import TextFieldF from '../../../../../components/Fields/TextFieldF';

const ADDRESS = STRINGS.APPLICATION.PERSONAL_LOAN.ADDRESS;

const useStyles = makeStyles({
  bottomSpacing: {
    marginBottom: MRG_BOTT.bottomSpacing
  }
});

interface Props extends ApplicationProps {
  tprovs: IProvince[],
  tdists: IDistrict[],
  tcounties: ICounty[]
}

function Form(props: Props) {
  const classes = useStyles();
  const { tprovs, tdists, tcounties } = props;
  const { application: { borrower }, setApplication } = props;

  const [prov, setProv] = React.useState(borrower.province);
  const [dist, setDist] = React.useState(borrower.district);
  const [dists, setDists] = React.useState<IDistrict[]>([]);
  const [countys, setCountys] = React.useState<ICounty[]>([]);  

  useEffect(()=>{
    if(tdists !== undefined)
      setDists(tdists.filter(item => item.idProv === Number(prov)))
  },[prov])

  useEffect(()=>{
    if(tcounties !== undefined)
      setCountys(tcounties.filter(item => item.idProv === Number(prov) && item.idDist === Number(dist)))
  },[dist])  

  function onApplicationChange(key: string, value: string) {
    setApplication({
      borrower: {
        [key]: value
      }
    })
  }

  function onProvChange(event: React.ChangeEvent<{ value: unknown }>) {
    const prov = event.target.value as string
    setProv(prov)
    setApplication({
      borrower: {
        'province': prov
      }
    })
  }

  function onDistChange(event: React.ChangeEvent<{ value: unknown }>) {
    const dist = event.target.value as string
    setDist(dist)
    setApplication({
      borrower: {
        'district': dist
      }
    })
  } 

  function onCountyChange(event: React.ChangeEvent<{ value: unknown }>) {
    const county = event.target.value as string
    setApplication({
      borrower: {
        'county': county
      }
    })
  } 

  return (
    <Grid container spacing={2} justify='center'>
      <Grid container item xs={12} sm={11}>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label={ADDRESS.STREET}
            onChange={(value: string) => onApplicationChange('street', value)}
            value={borrower.street}
            error={!borrower.street}
            message={!borrower.street ? "Este es un Error de Prueba ...": ""}
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldPhone
            label={ADDRESS.PHONE_NUMBER}
            onChange={(value: string) => onApplicationChange('residentialNumber', value)}
            value={borrower.residentialNumber}
          />
        </Grid> 
        <Grid container item xs={12} spacing={2} classes={{item: classes.bottomSpacing}}>
          <InputLabel id="">{ADDRESS.PROVINCE}</InputLabel>
          <Select
            fullWidth
            type='text'
            value={borrower.province}
            onChange={onProvChange}
          >
            {tprovs.map((item, idx) => <MenuItem key={idx} value={item.id} selected = {item.id === Number(borrower.province)}>{item.name}</MenuItem>)}
          </Select>
        </Grid>

        <Grid container item xs={12} spacing={2} classes={{item: classes.bottomSpacing}}>
          <InputLabel id="">{ADDRESS.DISTRICT}</InputLabel>
          <Select
            fullWidth
            type='text'
            value={borrower.district}
            onChange={onDistChange}
          >
            {dists.map((item, idx) => <MenuItem key={idx} value={item.id} selected = {item.id === Number(borrower.district)}>{item.name}</MenuItem>)}
          </Select>
        </Grid>

        <Grid container item xs={12} spacing={2} classes={{item: classes.bottomSpacing}}>
          <InputLabel id="">{ADDRESS.COUNTY}</InputLabel>
          <Select
            fullWidth
            type='text'
            value={borrower.county}
            onChange={onCountyChange}
          >
            {countys.map((item, idx) => <MenuItem key={idx} value={item.id} selected = {item.id === Number(borrower.county)}>{item.name}</MenuItem>)}
          </Select>
        </Grid>
      </Grid>
    </Grid>
  );
}

export default Form;