import React from 'react';
import Grid from '@material-ui/core/Grid';
import FormLabel from '@material-ui/core/FormLabel';
import { makeStyles } from '@material-ui/core/styles';

import { TextField, CurrencyField, PhoneNumberField } from '../../../../../components/Fields';
import STRINGS from '../../../../../strings';
import { Props } from '../index';
import { MRG_BOTT } from '../../../../Quotation/PersonalLoan/types';
import TextFieldF from '../../../../../components/Fields/TextFieldF';

const { WORK } = STRINGS.APPLICATION.PERSONAL_LOAN;

const useStyles = makeStyles({
  bottomSpacing: {
    marginBottom: MRG_BOTT.bottomSpacing
  },
  label: {
    marginBottom: MRG_BOTT.label
  }
});

function Work(props: Props) {
  const classes = useStyles();
  const { application: { borrower }, setApplication } = props;

  function onApplicationChange(key: string, value: string) {
    setApplication({
      borrower: {
        [key]: value
      },
    });
  }

  var elemento = document.getElementById( "top" );
  if(elemento) {
    elemento.scrollIntoView(true);
  }


  return (
    <Grid id="top" container spacing={2} justify='center'>
      <Grid container item xs={12} sm={11}>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label={WORK.COMPANY}
            onChange={(value: string) => onApplicationChange('company', value)}
            value={borrower.company}
            error={!borrower.company}
            message={!borrower.company ? "Este es un campo Obligatorio ...": ""}            
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label={WORK.POSITION}
            onChange={(value: string) => onApplicationChange('position', value)}
            value={borrower.position}
            error={!borrower.position}
            message={!borrower.position ? "Este es un campo Obligatorio ...": ""}  
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label={WORK.ADDRESS}
            onChange={(value: string) => onApplicationChange('companyAddress', value)}
            value={borrower.companyAddress}
            error={!borrower.companyAddress}
            message={!borrower.companyAddress ? "Este es un campo Obligatorio ...": ""}  
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <PhoneNumberField
            label={WORK.PHONE_NUMBER + "       + Ext."}
            onPhoneNumberChange={(value: string) => onApplicationChange('companyPhoneNumber', value)}
            phoneNumber={borrower.companyPhoneNumber}
            onExtensionChange={(value: string) => onApplicationChange('companyPhoneExtension', value)}
            extension={borrower.companyPhoneExtension}
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextField
            label={WORK.PREVIOUS_COMPANY}
            onChange={(value: string) => onApplicationChange('previousCompany', value)}
            value={borrower.previousCompany}
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.label}}>
          <FormLabel>{WORK.WAGE}</FormLabel>
        </Grid>
        <Grid item xs={12} sm={6} classes={{item: classes.bottomSpacing}}>
          <CurrencyField
            label={'Mensuales'}
            onChange={(value: string) => onApplicationChange('wage', value)}
            value={borrower.wage}
            endLabel
          />
        </Grid>
      </Grid>
    </Grid>
  );
}

export default Work;