import React from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { makeStyles, Theme } from '@material-ui/core/styles';
import { PillRadioButtons } from '../../../../../components/RadioButtons';
import { DigitalSignature } from '../DigitalSignature';

import { Props as ApplicationProps } from '../index';
import { iFecha } from './index';

const useStyles = makeStyles((theme: Theme) => ({
  container: {
    display: 'flex', 
    flexDirection: 'column', 
    height: '100%', 
    paddingTop: 72, 
    paddingBottom: 72,
    [theme.breakpoints.down('sm')]: {
      paddingTop: 24, 
      paddingBottom: 24
    }
  },
  title: {
    display: 'flex',
    alignItems: 'center',
    marginBottom: 72
  },
  subtitle: {
    '& span': {
      fontWeight: theme.typography.fontWeightBold,
      color: theme.palette.primary.main
    }
  },
  content: {
    display: 'block',
    textAlign:'center',
    marginBottom: 40,
    justifyContent: 'center',
  },
  option: {
    display: 'block',
    justifyContent: 'center'
  }
}));

interface Props extends ApplicationProps {
  fecha: iFecha;
}

let nombre="";
let cedula="";

function Form(props: Props) {
  const classes = useStyles();
  const { application: { borrower }, setApplication, fecha } = props;
  const dataQtt = window.localStorage.getItem('quotation');

  if (dataQtt) {
    const dataAttObj = JSON.parse(dataQtt)
    const { user } = dataAttObj
    nombre = user.name
  }
  cedula = borrower.id === undefined ? '' : borrower.id 

  async function onSignedSelected(accept: number) {
    setApplication({
      borrower: { accept },
    });
    return 
  }

  const options = [
    { label: "Aceptar", value: 1 },
    { label: "Rechazar", value: 0 }
  ]

  var elemento = document.getElementById( "top" );
  if(elemento) {
    elemento.scrollIntoView(true);
  }
  
  return (
    <>
      <Grid container justify='center'>
        <Grid item xs={12} sm={8} classes={{item: classes.content}}>
          <Typography 
            align='center' 
            variant='h5' 
            paragraph
          >
          </Typography>
          <Typography 
            align='center' 
            variant='h4' 
            paragraph
          >
            Autorizacin de la APC
          </Typography>
          <Typography
              align='justify' 
              color='textSecondary'
          >
<Typography component={'span'}>
Panam, {fecha.hoyes.split(',')[1]}
<br />
<br />
Seores<br />
<b>ACsoraT, S. A.</b><br />
Panam<br />
<br />
<br />
<br />
Estimado seor: <b>{nombre}</b>
<br />
<br />
Por este medio autorizo(amos) expresamente a <b>ACsoraT, S. A.</b>, sus 
subsidiarias y/o afiliadas, cesionarios o sucesoras, as como cualquier compaa 
que por operacin de cesin, administracin o compra de cartera adquiera los 
derechos de mi crdito, a que de conformidad con lo expresado en el artculo 24 
y dems disposiciones aplicables de la Ley 24 de 22 de mayo de 2002, solicite, 
consulte, recopile, intercambie y transmita a cualquier agencia de informacin de 
datos,   bancos   o   agentes   econmicos   informaciones   relacionadas   con 
obligaciones o transacciones crediticias que mantengo o pudiera mantener con 
dichos agentes econmicos de la localidad, sobre mi(nuestros) historial de 
crdito y relaciones con acreedores.
Tambin  queda  facultado  el  ACsoraT, S. A.,  sus  subsidiarias  y/o 
afiliadas, cesionarios o sucesoras, as como cualquier compaa que por una 
operacin de cesin, administracin o compra de cartera adquiera los derechos 
de   mi   crdito,   a   que   solicite   y   obtenga   informacin   de   instituciones 
gubernamentales relacionadas con las obligaciones o transacciones crediticias 
arriba referidas.
As  mismo,  exonero(amos)  de  cualquier  consecuencia  o  responsabilidad resultante del ejercicio de solicitar o suministrar informacin, o por razn de cualesquiera autorizaciones contenidas en la presente carta, al ACsoraT, S. A, a sus compaas afiliadas, subsidiarias, cesionarios y/o sucesoras, a sus empleados, ejecutivos, directores dignatarios o apoderados, as como cualquier compaa que por una operacin de cesin, administracin o compra de cartera adquiera los derechos de mi crdito.
<br />
<br />
Atentamente,
<br />
<br />
<br />
<br />
Nombre: <u>{nombre}</u> 
<br />
Cdula: <u>{cedula}</u>
<br />
<br />
<br />
<br />
Fundamento legal: Ley 24 de 22 de mayo de 2002.
<br />
</Typography>
          </Typography>
          <DigitalSignature {...props}/>
        </Grid>

        <Grid item xs={12} sm={10} classes={{root: classes.option}}>
          <PillRadioButtons
            label={""}
            onClick={onSignedSelected}
            options={options}
            value={borrower.accept}
          />
        </Grid>
      </Grid>
    </>
  );
}

export default Form;