import React from 'react';
import Grid from '@material-ui/core/Grid';
import FormLabel from '@material-ui/core/FormLabel';
import { makeStyles } from '@material-ui/core/styles';

import STRINGS from '../../../../../strings';
import { CurrencyField } from '../../../../../components/Fields';
import { Props } from '../index';

const useStyles = makeStyles({
  bottomSpacing: {
    marginBottom: 48
  },
  label: {
    marginBottom: 36
  },
});

function Income(props: Props) {

  const classes = useStyles();
  const { quotation: { borrower }, setQuotation } = props;

  function onWageChange(wage: string) {
    setQuotation({ borrower: { wage: Number(wage) || undefined } });
  }

  function onAlloanceChange(alloance: string) {
    setQuotation({ borrower: { alloance: Number(alloance) || undefined } });
  }

  function onPerDiemChange(perDiem: string) {
    setQuotation({ borrower: { perDiem: Number(perDiem) || undefined } });
  }

  return (
    <Grid container spacing={2} justify='center'>
      <Grid item xs={11}>
        <Grid item xs={12} classes={{item: classes.label}}>
          <FormLabel>{STRINGS.QUOTATION.PERSONAL_LOAN.INCOME.CHOOSE_INCOME}</FormLabel>
        </Grid>
        <Grid item xs={12} sm={8} classes={{item: classes.bottomSpacing}}>
          <CurrencyField
            label={STRINGS.QUOTATION.PERSONAL_LOAN.INCOME.WAGE}
            onChange={onWageChange}
            value={borrower.wage}
            />
        </Grid>
        <Grid item xs={12} sm={8} classes={{item: classes.bottomSpacing}}>
          <CurrencyField
            label={STRINGS.QUOTATION.PERSONAL_LOAN.INCOME.ALLOANCE}
            onChange={onAlloanceChange}
            value={borrower.alloance}
          />
        </Grid>
        <Grid item xs={12} sm={8} classes={{item: classes.bottomSpacing}}>
          <CurrencyField
            label={STRINGS.QUOTATION.PERSONAL_LOAN.INCOME.PER_DIEM}
            onChange={onPerDiemChange}
            value={borrower.perDiem}
          />
        </Grid>
      </Grid>
    </Grid>
  );
};

export default Income;