import React from 'react';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';

import { PhoneNumberField,  } from '../../../../../components/Fields';
import STRINGS from '../../../../../strings';
import { Props } from '../index';
import { MRG_BOTT } from '../../../../Quotation/PersonalLoan/types';
import TextFieldPhone from '../../../../../components/Fields/TextFieldPhone';
import TextFieldCelPhone from '../../../../../components/Fields/TextFieldCelPhone';
import TextFieldF from '../../../../../components/Fields/TextFieldF';

const { PERSONAL_REFERENCE_RELATED } = STRINGS.APPLICATION.PERSONAL_LOAN;

const useStyles = makeStyles({
  bottomSpacing: {
    marginBottom: MRG_BOTT.bottomSpacing
  }
});

function PersonalReferenceRelated(props: Props) {
  const classes = useStyles();
  const { application: { personalReferenceRelated }, setApplication } = props;

  function onApplicationChange(key: string, value: string) {
    setApplication({
      personalReferenceRelated: {
        [key]: value
      },
    });
  }

  return (
    <Grid container spacing={2} justify='center'>
      <Grid container item xs={12} sm={11}>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label={PERSONAL_REFERENCE_RELATED.NAME}
            onChange={(value: string) => onApplicationChange('name', value)}
            value={personalReferenceRelated.name}
            error={!personalReferenceRelated.name}
            message={!personalReferenceRelated.name ? "Este es un campo Obligatorio ...": ""}  
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label={PERSONAL_REFERENCE_RELATED.LAST_NAME}
            onChange={(value: string) => onApplicationChange('lastName', value)}
            value={personalReferenceRelated.lastName}
            error={!personalReferenceRelated.lastName}
            message={!personalReferenceRelated.lastName ? "Este es un campo Obligatorio ...": ""}  
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label={PERSONAL_REFERENCE_RELATED.RELATIONSHIP}
            onChange={(value: string) => onApplicationChange('relationship', value)}
            value={personalReferenceRelated.relationship}
            error={!personalReferenceRelated.relationship}
            message={!personalReferenceRelated.relationship ? "Este es un campo Obligatorio ...": ""}  
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldPhone
            label={PERSONAL_REFERENCE_RELATED.RESIDENCE_PHONE_NUMBER}
            onChange={(value: string) => onApplicationChange('residenceNumber', value)}
            value={personalReferenceRelated.residenceNumber}
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldCelPhone
            label={PERSONAL_REFERENCE_RELATED.PHONE_NUMBER}
            onChange={(value: string) => onApplicationChange('phoneNumber', value)}
            value={personalReferenceRelated.phoneNumber}
          />
        </Grid>

        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label={PERSONAL_REFERENCE_RELATED.COMPANY}
            onChange={(value: string) => onApplicationChange('company', value)}
            value={personalReferenceRelated.company}
            error={!personalReferenceRelated.company}
            message={!personalReferenceRelated.company ? "Este es un campo Obligatorio ...": ""}  
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <PhoneNumberField
            label={PERSONAL_REFERENCE_RELATED.COMPANY_PHONE_NUMBER}
            onPhoneNumberChange={(value: string) => onApplicationChange('companyPhoneNumber', value)}
            phoneNumber={personalReferenceRelated.companyPhoneNumber}
            onExtensionChange={(value: string) => onApplicationChange('companyPhoneExtension', value)}
            extension={personalReferenceRelated.companyPhoneExtension}
          />
        </Grid>
      </Grid>
    </Grid>
  );
}

export default PersonalReferenceRelated;