import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import {
  STEPS,
  JOB_SECTORS,
  Quotation,
  PRIVATE_OCCUPATION,
  PUBLIC_OCCUPATION,
  LABORAL_STATUS,
  PAYMENT_FRECUENCY,
  AGE_LIMIT,
  IMC_MAX,
  RESIDENCE_TYPES
} from "../types";
import Forms from '../Forms';


interface Stepper {
  next?: () => void;
  prev?: () => void;
  disabled: boolean;
  Form: React.ReactElement;
};

function useStep(
  quotation: Quotation, 
  setQuotation: (quotation: Partial<Quotation>) => void
): Stepper 
{
  let history = useHistory();
  const [step, setStep] = useState(STEPS.JOB_SECTOR);

  const API_HOST = ''; //process.env.REACT_APP_API_HOST;
  const finishQuote = async (causas = "") => {
  
    history.push('/cotizar/finalizar/?causas='+causas);
  }

  const props = {
    quotation, 
    setQuotation
  };

  //***************************************//
  //***************************************//
  //** Guarda Informacion del Tracking  **//
  //***************************************//
  //***************************************//
  const saveTracking = async (tracking = "") => {
    const { quotation: { user: { 
      fname="", lname="", email="", cellphone="", terms_cond = false }}} = props;

    const { quotation: { loan: { bank = "0", opciones = "" }}} = props;  

    let jobSector: JOB_SECTORS | undefined, 
        residenceType: RESIDENCE_TYPES | undefined, 
        contractType: LABORAL_STATUS | undefined

    jobSector = quotation.borrower.jobSector || undefined
    residenceType = quotation.borrower.residenceType
    contractType = quotation.borrower.contractType

    const { quotation: { borrower: { 
      profession="0", institution="0", occupation="0", retirement="0",
      creditHistory=null, paymentFrecuency="",
      currentJobMonths=0,
      previousJobMonths='0',
      wage=0, 
      alloance=0, 
      perDiem=0,
      gender="", 
      birthDate="",
      monthlyResidenceFee=0,
    }}} = props;

    let birth = new Date();
    if (birthDate !== undefined) {
      birth = new Date(birthDate)
    }

    let body = {
      Tracking: tracking.toString(),
      id_param: "",
      Nombre: fname,
      Apellido: lname,
      Email: email,
      Celular: cellphone,
      Genero: gender,
      Fecha_Nac: birth.toString(),
      Terminos_Condiciones: terms_cond ? "Si" : "No",

      Sector: (jobSector || '').toString(), 
      Profesion: profession, 
      Institucion: institution,
      Tipo_Residencia: (residenceType || '').toString(), 
      Mensualidad: (monthlyResidenceFee || '').toString(),
      Meses_Trabajo_Actual: currentJobMonths.toString(),
      Tipo_Contrato: (contractType || '').toString(),
      Meses_Trabajo_Anterior: previousJobMonths.toString(),
      Historial_Credito: (creditHistory || '').toString(), 
      Frecuencia_Pago: paymentFrecuency,

      Salario: wage.toString(), 
      Servicios_Profesionales: alloance.toString(), 
      Viaticos: perDiem.toString(),

      Entidad_Seleccionada: bank.toString(),
    }

    let ID = null
    let method = "POST"
    try {
      let response = await fetch(`${API_HOST}/api/tracking/${email}`, {
        method: "GET"
      })
      let result = await response.json();

      if(result.length > 0) {
        ID = result[0]._id
        method = "PUT"
        body.id_param = ID
      }
    } catch (error) {
      console.log(error)
    }
    
    try {
      let res: Response = await fetch(`${API_HOST}/api/tracking`, {
        method: `${method}`,
        body: new URLSearchParams(body),
      })
  
      // METERLE UN TRY CATCH
      const result = await res.json();
      console.log("Method: ===============> ", method, ID)
      if(method === "POST") {
        window.localStorage.setItem("IDMongo",  JSON.stringify(result))
      } else {
        window.localStorage.setItem("IDMongo",  JSON.stringify({ "ID": ID }))
      }
    } catch (error) {
      console.log(error)
    }
    
    //***********************************************************//
    //***********************************************************//
    // Buscar Token de Clientify
    //***********************************************************//
    //***********************************************************//
    let Token = null
    let Clientify = window.localStorage.getItem('Clientify') || ''
    if (Clientify) {
      Token = JSON.parse(Clientify).token
    }

    if (!Token) {
      const response: Response = await fetch("/api/clientify-token", {
        method: 'POST'
      })
  
      // Guardamos Token de Clientify
      const data = await response.json();
      Token = data.token
      window.localStorage.setItem("Clientify",  JSON.stringify(data))
    }

    const IDClientify = window.localStorage.getItem('IDClientify') || ''

    // Guardamos Datos del Contacto
    var raw = {
      "token": Token,
      "ID": IDClientify,
      "Tracking": tracking.toUpperCase(),
      "first_name": fname,
      "last_name": lname,
      "email": email,
      "phone": cellphone,
      "fecha_nacimiento": birth.toString(),
      "acepta_terminos_condiciones": terms_cond ? "Si" : "No",
      "contrato_laboral": (contractType || '0') === '0' ? 'Temporal' : (contractType || '0') === '1' ? 'Permanente' : 'Serv. Profesional',
      "meses_trabajo_actual": currentJobMonths.toString(),
      "meses_trabajo_anterior": previousJobMonths.toString(),
      "Salario": wage.toString(),
      "Sector": jobSector === 'Pb' ? 'Pblico': jobSector === 'P' ? 'Privado' : 'Jubilado',
      "Institucion": institution,
      "Ocupacion": occupation,
      "Planilla": retirement,
      "Profesion": profession,
      "Genero": gender === 'male' ? "Hombre": "Mujer",
      "tipo_residencia": (residenceType || '').toString(),
      "mensualidad_casa": monthlyResidenceFee.toString(),
      "entidad_seleccionada": bank.toString(),
      "prestamo_opciones": JSON.stringify(opciones),

      "province": "0",
      "district": "0",
      "county": "0",
      "street": "N/A",
    };

    method = "POST"
    if(IDClientify) method = "PUT"

    try {
      const response = await fetch("/api/clientify", {
        method: method,
        body:  new URLSearchParams(raw)
      })
  
      const result = await response.json();
      // Guardamos id del contacto en Clientify
      window.localStorage.setItem("IDClientify",  JSON.stringify(result.id))
    } catch (error) {
      console.log(error)
    }

    return
  }

  // Movido a la parte de arriba
  // const props = {
  //   quotation, 
  //   setQuotation
  // };

  useEffect(() => {
    window.localStorage.removeItem('financ');
    const dataApp = window.localStorage.getItem('quotation');
    if (dataApp) {
      setQuotation(JSON.parse(dataApp))
      setQuotation({
        borrower: {
          jobSector: undefined,
          profession: undefined,
          occupation: undefined,
          retirement: undefined,
          institution: undefined
        },
        finance: {
          entity: undefined
        },
        loan: {
          opciones: undefined,
        }
      })
    }
  }, [])

  return ({
    [STEPS.JOB_SECTOR]: () => ({
      next: () => setStep(STEPS.OCCUPATION),
      disabled: !quotation.borrower.jobSector,
      Form: <Forms.JobSector {...props} />
    }),
    [STEPS.OCCUPATION]: () => {
      switch (quotation.borrower.jobSector) {
        case JOB_SECTORS.PRIVATE:
          return ({
            next: () => setStep(STEPS.EMPLOYMENT),
            prev: () => setStep(STEPS.JOB_SECTOR),
            disabled: (() => {
              const { profession, occupation } = quotation.borrower;
              const occupationRequired = PRIVATE_OCCUPATION.HEALTHCARE === profession;

              return (
                !profession || 
                (occupationRequired && !occupation)
              );
            })(),
            Form: <Forms.Occupation {...props} />
          });
        case JOB_SECTORS.PUBLIC:
          return ({
            next: () => setStep(STEPS.EMPLOYMENT),
            prev: () => setStep(STEPS.JOB_SECTOR),
            disabled: (() => {
              const { occupation, profession, institution } = quotation.borrower;
              const occupationRequired = (
                PUBLIC_OCCUPATION.HEALTHCARE === profession ||
                PUBLIC_OCCUPATION.ACP === profession ||
                PUBLIC_OCCUPATION.SECURITY === profession
              );
              const institutionRequired = PUBLIC_OCCUPATION.ADMINISTRATION === profession;

              return (
                !profession || 
                (occupationRequired && !occupation) ||
                (institutionRequired && !institution)
              );
            })(),
            Form: <Forms.Occupation {...props} />
          });
        case JOB_SECTORS.RETIRED:
          return ({
            next: () => setStep(STEPS.CREDIT_HISTORY),
            prev: () => setStep(STEPS.JOB_SECTOR),
            disabled: !quotation.borrower.retirement,
    
            Form: <Forms.Occupation {...props} />
          });
        default:
          return ({
            disabled: false,
            Form: <Forms.Occupation {...props} />
          });
      }
    },
    [STEPS.EMPLOYMENT]: () => ({
      next: () => {
        const { contractType, currentJobMonths = 0, 
              jobSector, profession = '0' } = quotation.borrower;
   
        const isPermanent = contractType === LABORAL_STATUS.PERMANENT;
        window.localStorage.removeItem('financomer-status'); 
        if (!isPermanent) {
          if(profession === '5') {
            setQuotation({finance: {entity: 'financomer'}}) 
            window.localStorage.setItem('financomer-status', 'termporal');     
          } else return finishQuote("Combinacion Financomer con ACP (Autoridad del Canal)");
        }

        // Condicion de Sector Pblico 
        // Prefesiones: Medico/Enfermera/Educadores y Administrativos
        if (currentJobMonths < 1 && jobSector === 'Pb'
                && (profession === '2' || profession === '3' || profession === '4')) {
          return finishQuote("Sector Publico: Medico/Enfe/Educ/Admtvo con cero (0) meses en el trabajo actual");
        }

        // Condicion de Sector Privado 
        // Prefesiones: Medico/Enfermera/Educadores y Administrativos
        if (currentJobMonths < 6 && jobSector === 'P') {
          return finishQuote("Sector Privado: Medico/Enfe/Educ/Admtvo con menos de seis (6) meses en el trabajo actual");
        }        

        // El historial meses solo aplica parq el Sector Privado
        const previousJobMonthsRequired = currentJobMonths < 24 && jobSector === 'P' //&& finance !== 'financomer'
        if (isPermanent && previousJobMonthsRequired) {
          return setStep(STEPS.PREVIOUS_JOB);
        }

        return setStep(STEPS.CREDIT_HISTORY);
      },
      prev: () => setStep(STEPS.OCCUPATION),
      disabled: (() => {
        const { contractType, currentJobMonths = 0 } = quotation.borrower;
        return (!contractType || !(currentJobMonths >= 0));
      })(),
      Form: <Forms.Employment {...props} />
    }),
    [STEPS.PREVIOUS_JOB]: () => ({
      next: () => {
        const { currentJobMonths = 0, previousJobMonths = 0 } = quotation.borrower;

        window.localStorage.removeItem('financomer-prev-jobs'); 
        if (currentJobMonths + previousJobMonths < 24) {
          // Condicion de Financomer
          window.localStorage.setItem('financomer-prev-jobs', 'No tiene');    
          setQuotation({finance: {entity: 'financomer'}}) 
          //return finishQuote();
        }

        return setStep(STEPS.CREDIT_HISTORY)
      },
      prev: () => setStep(STEPS.EMPLOYMENT),
      disabled: !quotation.borrower.previousJobMonths,
      Form: <Forms.PreviousJob {...props} />
    }),
    [STEPS.CREDIT_HISTORY]: () => ({
      next: () => {
        const { creditHistory, paymentFrecuency } = quotation.borrower;
        const lessThanSixtyDays = (
          PAYMENT_FRECUENCY.LESS_THAN_30_DAYS === paymentFrecuency ||
          PAYMENT_FRECUENCY.LESS_THAN_60_DAYS === paymentFrecuency
        );

        // const { jobSector, profession = '0', institution= '0', occupation = '0', retirement = '0' } = quotation.borrower;
        // console.log({jobSector, profession, institution, occupation, retirement})

        window.localStorage.removeItem('financomer-historial'); 
        if (!creditHistory || !lessThanSixtyDays) {
          // Condicion de Financomer
          window.localStorage.setItem('financomer-historial', 'No tiene');    
          setQuotation({finance: {entity: 'financomer'}}) 
          // return finishQuote();
        }

        return setStep(STEPS.INCOME)
      },
      prev: () => {
        const { contractType, currentJobMonths = 0, jobSector } = quotation.borrower;

        const jobTimeRequired = contractType === LABORAL_STATUS.PERMANENT;
        // El historial precio solo aplica parq el Sector Privado
        const previousJobMonthsRequired = currentJobMonths < 24 && jobSector === 'P'
        if (jobTimeRequired && previousJobMonthsRequired) {
          return setStep(STEPS.PREVIOUS_JOB);
        }

        const isRetired = jobSector === JOB_SECTORS.RETIRED;
        if (isRetired) {
          return setStep(STEPS.OCCUPATION)
        }

        return setStep(STEPS.EMPLOYMENT)
      },
      disabled: (() => {
        const { creditHistory, paymentFrecuency } = quotation.borrower;
        if (creditHistory) {
          return creditHistory && !paymentFrecuency;
        }

        return typeof creditHistory === 'undefined';
      })(),
      Form: <Forms.CreditHistory {...props} />
    }),
    [STEPS.INCOME]: () => ({
      next: () => {
        const { wage = 0, alloance = 0, perDiem = 0, jobSector, profession } = quotation.borrower;
        const totals = wage + alloance + perDiem;

        window.localStorage.removeItem('financomer-income'); 
        if(JOB_SECTORS.PRIVATE === jobSector) {
          if(totals < 300) {
            return finishQuote("Sector privado: Salario menor de 300.00");
          }
          // if(totals < 500) {
          //   if(totals >= 300) {
          //     // Condicion de Financomer
          //     window.localStorage.setItem('financomer-income', 'Menos de 500');
          //     setQuotation({finance: {entity: 'financomer'}}) 
          //   } else return finishQuote("Sector privado:  Salario menor de 500.00 (Med/Enf/Edu)");
          // } else if(profession === PRIVATE_OCCUPATION.OTHER) {
          //   if(totals < 700) {
          //     if(totals >= 500) {
          //       // Condicion de Financomer
          //       window.localStorage.setItem('financomer-income', 'Menos de 700');
          //       setQuotation({finance: {entity: 'financomer'}}) 
          //     } finishQuote("Sector privado: Salario menor de 700.00 (Empresa Privada)");
          //   }
          // }
        }
        if(JOB_SECTORS.PUBLIC === jobSector) {
          if(totals < 300) {
            return finishQuote("Sector pblico: Salario menor de 300.00");
          }
          // if(totals < 400) {
          //   if(totals >= 300) {
          //     // Condicion de Financomer
          //     window.localStorage.setItem('financomer-income', 'Menos de 400');
          //     setQuotation({finance: {entity: 'financomer'}}) 
          //   } else return finishQuote("Sector privado: Salario menor de 400.00");
          // } else if(profession === PUBLIC_OCCUPATION.ACP) {
          //   if(totals < 1000) {
          //     return finishQuote("Sector privado: Salario menor de 1000.00 ACP (Autoridad del Canal)");
          //   }
          // }
        }
        if(JOB_SECTORS.RETIRED === jobSector) {
          if(totals < 175) {
            return finishQuote("Sector jubilado: Salario menor de 175.00");
          }
        }
        setStep(STEPS.RESIDENCE)
      },
      prev: () => setStep(STEPS.CREDIT_HISTORY),
      disabled: !quotation.borrower.wage,
      Form: <Forms.Income {...props} />
    }),
    [STEPS.RESIDENCE]: () => ({
      next: () => setStep(STEPS.PROFILE),
      prev: () => setStep(STEPS.INCOME),
      disabled: (() => {
        const { residenceType, monthlyResidenceFee } = quotation.borrower;
        return (!residenceType || (residenceType===RESIDENCE_TYPES.MORTGAGE && !monthlyResidenceFee));        
      })(),
      Form: <Forms.Residence {...props} />
    }),
    [STEPS.PROFILE]: () => ({
      // LR-2021-03 Se agrega caclulo del IMC y la Edad
      next: () => {
        const { gender, birthDate, height, heightUnit, weight, weightUnit } = quotation.borrower;

        window.localStorage.setItem('gender', gender || '');

        let xweight = weight === undefined ? 100 : weight
        let xheight = height === undefined ? 1 : height
        if(weightUnit === 'lb') xweight = xweight / 2.205 // Converted to Meters
        if(heightUnit === 'ft') xheight = xheight / 3.281 // Converted to Kilos
        
        // Formula  IMC = Peso(kg)/Estatura(mts)^2
        const IMC = xweight/xheight**2;

        const today = new Date() ; //  Ver como traerla del servidor
        let birth = new Date();
        if (birthDate !== undefined) {
          birth = new Date(birthDate)
        }
        const age = today.getFullYear() - birth.getFullYear() - 1;  

        window.localStorage.setItem('Edad', age.toString() || '0');
        //********************** */
        console.log({ 'IMC': IMC, 'Edad': age, 'Peso': xweight, 'Estatura': xheight, 'Genero': gender} );
        //********************* */

        if (IMC > IMC_MAX || !(age >= AGE_LIMIT.MIN_AGE && age <= AGE_LIMIT.MAX_AGE)) {
          return finishQuote(`Indice de Grasa Corporal o Edad Maxima: IMC (${IMC}) o Edad (${age})`);
        } 
        // LR-2021-03 Se agrega caclulo del IMC y la Edad
        // console.log(quotation.finance)

        const { jobSector } = quotation.borrower;
        // console.log(jobSector, age, AGE_LIMIT.JUB_MAN, AGE_LIMIT.JUB_WOM)
        if(JOB_SECTORS.RETIRED !== jobSector) {
          if(age > AGE_LIMIT.JUB_WOM && gender === 'female') {
            return finishQuote(`Limite Maximo de Edad Jubilado Mujer: ${age}`);
          }
          if(age > AGE_LIMIT.JUB_MAN && gender === 'male') {
            return finishQuote(`Limite Maximo de Edad Jubilado Hombre: ${age}`);
          }
        }

        // GUARDAR EN MONGODB
        setQuotation({ tracking: "Perfile del Prospecto" })
        saveTracking("Perfile del Prospecto")
        window.localStorage.setItem('quotation', JSON.stringify(quotation))
        window.localStorage.setItem('email', quotation.user.email || '');
        setStep(STEPS.TERMS)
      },
      prev: () => setStep(STEPS.RESIDENCE),
      disabled: (() => {
        const { gender, birthDate, height, weight } = quotation.borrower;
        const { fname, lname, email, cellphone, terms_cond } = quotation.user;

        return (!gender || !birthDate || !height || !weight || !terms_cond || !fname || !lname || !email || !cellphone);
      })(),

      Form: <Forms.Profile {...props} />
    }),
    [STEPS.TERMS]: () => ({
      next: () => {
        // let { amount=0, mount_min=0, mount_max=0 } = quotation.loan;
        // if(amount < mount_min || amount > mount_max) {
        //   console.log(`*** Monto del Prstamo: ${amount}, fuera del Rango: (${mount_min} - ${mount_max}).`)
        //   return finishQuote();
        // }
        setQuotation({ tracking: "Monto Prstamo" })
        saveTracking("Monto Prstamo")
        window.localStorage.setItem('quotation', JSON.stringify(quotation));
        setStep(STEPS.BANKS)
      },
      disabled: false,
      Form: <Forms.Terms {...props} />
    }),
    [STEPS.BANKS]: () => ({
      next: () => {
        setQuotation({ tracking: "Seleccin del Banco o Financiera" })
        saveTracking("Seleccin del Banco o Financiera")
        window.localStorage.setItem('quotation', JSON.stringify(quotation))
        window.localStorage.setItem('ruta', quotation.loan.bank || '')

        console.log(quotation)
        setStep(STEPS.REASON)
      },
      prev: () => setStep(STEPS.TERMS),
      disabled: !quotation.loan.bank,
      Form: <Forms.Banks {...props} />
    }),
    [STEPS.REASON]: () => ({
      next: () => {
        setQuotation({ tracking: "Propsito del Prstamo" })
        saveTracking("Propsito del Prstamo" )
        setStep(STEPS.LOGIN)
      },
      prev: () => setStep(STEPS.BANKS),
      disabled: !quotation.loan.reason,
      Form: <Forms.Reason {...props} />
    }),
    [STEPS.LOGIN]: () => ({
      next: () => {
        setQuotation({ 
          tracking: "Login a Redes Sociales"
        })
        saveTracking("Login a Redes Sociales")
        setStep(STEPS.REGISTER)
      },
      prev: () => setStep(STEPS.REASON),
      disabled: (() => {
        const { fname, lname, email, origin, idUser  } = quotation.user
        return ((!fname || !lname || !email || !origin) && (!origin || !idUser));        
      })(),
      Form: <Forms.Login {...props} />
    }),
    [STEPS.REGISTER]: () => ({
      next: () => {
        // console.log(quotation)
        setQuotation({ tracking: "Registro Completo" })
        saveTracking("Registro Completo")
        window.localStorage.setItem('quotation', JSON.stringify(quotation));

        history.push('/solicitud/prestamo-personal')  
      },
      prev: () => setStep(STEPS.LOGIN),
      disabled: (() => {
        const { fname, lname, lname_2, email, cellphone, nationality } = quotation.user;
        return (!fname || !lname || !lname_2 || !email || !cellphone || !nationality);        
      })(),
      Form: <Forms.Register {...props} />
    }),
  })[step]();
};

export default useStep;