import React from 'react';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';

import { PhoneNumberField } from '../../../../../components/Fields';
import STRINGS from '../../../../../strings';
import { Props } from '../index';
import { MRG_BOTT } from '../../../../Quotation/PersonalLoan/types';
import TextFieldPhone from '../../../../../components/Fields/TextFieldPhone';
import TextFieldCelPhone from '../../../../../components/Fields/TextFieldCelPhone';
import TextFieldF from '../../../../../components/Fields/TextFieldF';

const { PERSONAL_REFERENCE_UNRELATED } = STRINGS.APPLICATION.PERSONAL_LOAN;

const useStyles = makeStyles({
  bottomSpacing: {
    marginBottom: MRG_BOTT.bottomSpacing
  }
});

function PersonalReferenceUnrelated(props: Props) {
  const classes = useStyles();
  const { application: { personalReferenceUnrelated }, setApplication } = props;

  function onApplicationChange(key: string, value: string) {
    setApplication({
      personalReferenceUnrelated: {
        [key]: value
      },
    });
  }

  return (
    <Grid container spacing={2} justify='center'>
      <Grid container item xs={12} sm={11}>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label={PERSONAL_REFERENCE_UNRELATED.NAME}
            onChange={(value: string) => onApplicationChange('name', value)}
            value={personalReferenceUnrelated.name}
            error={!personalReferenceUnrelated.name}
            message={!personalReferenceUnrelated.name ? "Este es un campo Obligatorio ...": ""}
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label={PERSONAL_REFERENCE_UNRELATED.LAST_NAME}
            onChange={(value: string) => onApplicationChange('lastName', value)}
            value={personalReferenceUnrelated.lastName}
            error={!personalReferenceUnrelated.lastName}
            message={!personalReferenceUnrelated.lastName ? "Este es un campo Obligatorio ...": ""}
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldPhone
            label={PERSONAL_REFERENCE_UNRELATED.RESIDENCE_PHONE_NUMBER}
            onChange={(value: string) => onApplicationChange('residenceNumber', value)}
            value={personalReferenceUnrelated.residenceNumber}
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldCelPhone
            label={PERSONAL_REFERENCE_UNRELATED.PHONE_NUMBER}
            onChange={(value: string) => onApplicationChange('phoneNumber', value)}
            value={personalReferenceUnrelated.phoneNumber}
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label={PERSONAL_REFERENCE_UNRELATED.COMPANY}
            onChange={(value: string) => onApplicationChange('company', value)}
            value={personalReferenceUnrelated.company}
            error={!personalReferenceUnrelated.company}
            message={!personalReferenceUnrelated.company ? "Este es un campo Obligatorio ...": ""}
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <PhoneNumberField
            label={PERSONAL_REFERENCE_UNRELATED.COMPANY_PHONE_NUMBER}
            onPhoneNumberChange={(value: string) => onApplicationChange('companyPhoneNumber', value)}
            phoneNumber={personalReferenceUnrelated.companyPhoneNumber}
            onExtensionChange={(value: string) => onApplicationChange('companyPhoneExtension', value)}
            extension={personalReferenceUnrelated.companyPhoneExtension}
          />
        </Grid>
      </Grid>
    </Grid>
  );
}

export default PersonalReferenceUnrelated;