import React from 'react';
import { makeStyles,  } from '@material-ui/core/styles';
import { Props as ApplicationProps } from '../index';
import { iApc } from './index';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Checkbox from '@material-ui/core/Checkbox';

import IconButton from '@material-ui/core/IconButton';
import CalendarTodayIcon from '@material-ui/icons/CalendarToday';
import EventBusyIcon from '@material-ui/icons/EventBusy';
import SportsHandballIcon from '@material-ui/icons/SportsHandball';
import LocalAtmIcon from '@material-ui/icons/LocalAtm';

import CreditCardIcon from '@material-ui/icons/CreditCard';
import DriveEtaIcon from '@material-ui/icons/DriveEta';
import HouseIcon from '@material-ui/icons/House';
import HtmlToPdf from '../../../../../components/HtmlToPdf';

const useStyles = makeStyles((theme) => ({
  table: {
    width: "100%",
    overflowX: 'auto',
    
  },
  root: {
    flexGrow: 1
  },
  bottomRight: {
    justifyContent: "flex-end"
  },
  outerColumn: {
    borderRight: "1px solid grey",
    borderBottom: "1px solid grey",
    borderLeft: "1px solid grey"
  },
  centerColumn: {
    borderBottom: "1px solid grey"
  },
  margin: {
    margin: theme.spacing(1),
  },
}));

interface Props extends ApplicationProps {
  refApc: iApc[]
}

function Form(props: Props) {
  const classes = useStyles();
  const { application: { borrower }, setApplication, refApc } = props;

  let debt_capacity=0, debt_capacity_hip=0, discount_capacity_hip=0, salario=0, aRecibir=0, hipoteca_sn=false
  const dataApp = window.localStorage.getItem('quotation');
  if (dataApp) {
    const Quotation = JSON.parse(dataApp)
    aRecibir=Quotation.loan.amount
    debt_capacity=Quotation.loan.debt_capacity
    debt_capacity_hip=Quotation.loan.debt_capacity_mortgage
    discount_capacity_hip=Quotation.loan.discount_capacity_mortgage
    salario=Quotation.borrower.wage + ( Quotation.borrower.alloance || 0) + (Quotation.borrower.perDiem || 0)
    // console.log(debt_capacity, debt_capacity_hip, discount_capacity_hip, salario)

    // 1	Casa Propia
    // 2	Padres o Familiares
    // 3	Casa Hipotecada
    // 4	Casa Alquilada

    hipoteca_sn = Quotation.borrower.residenceType === "3"
  }
  //***************************************/
  //***************************************/
  // Datos para Pruebas de Calculo
  // aRecibir = 7075.27
  // discount_capacity_hip = 20
  // debt_capacity = 45
  // debt_capacity_hip = 50
  // salario = 1600

  //***************************************/
  const pjeEndTC = 55
  const pjeEndAuto = 60
  const pjeEndHip = 50

  const pjeCapPagoAuto = 35
  const letraMinAuto = 150
  //***************************************/
  // Prestamo autos
  const plazo = 84
  const tasa = 6.25
  const total_gastos = 795.63
  //***************************************/
  //***************************************/

  const id = borrower.id || ""
  const montoLoan = aRecibir
  const data = window.localStorage.getItem('APC-'+id)
  let onLine = false
  if(!data){
    const hoy = new Date()
    // const yyyy = hoy.getFullYear().toString()
    // const mm = (hoy.getMonth() + 3).toString()
    // const dd = hoy.getDate().toString()
    
    const expiresIn = hoy.getTime() + 2*30*24*60*60*1000
    window.localStorage.setItem('APC-'+id, JSON.stringify({ data: refApc, expiresIn: new Date(expiresIn) }))
    onLine = true
  }

  // console.log('refApc',refApc)
  refApc.sort(function (a, b) {
    if (Number(a.saldO_ACTUAL) < Number(b.saldO_ACTUAL)) {
      return 1;
    }
    if (Number(a.saldO_ACTUAL) > Number(b.saldO_ACTUAL)) {
      return -1;
    }
    // a must be equal to b
    return 0;
  });


  const fp0 = refApc.filter(f => f.descR_CORTA_RELA !== "SERVICIOS")
  for(let deuda of fp0) {
    if(deuda.descR_CORTA_RELA === "L. ROTATIVA" || deuda.descR_CORTA_RELA === "TMA"){
      deuda.importE_PAGO = (Number(deuda.saldO_ACTUAL) * 0.05).toString()
    }
  
    if(deuda.descR_CORTA_RELA === "TARJ. CREDITO"){
      deuda.importE_PAGO = (Number(deuda.saldO_ACTUAL) * 0.03).toString()
    }

    deuda.montoAplicado = 0.00
    deuda.montoARecibir = 0.00

  }
  let score = 0
  if(fp0.length > 0) score = Number(fp0[0].score || 0)
  // console.log(fp0)

  const fp1 = fp0.filter(f => f.descR_FORMA_PAGO === "DESCUENTO DIRECTO")
  const fp2 = fp0.filter(f => f.descR_FORMA_PAGO !== "DESCUENTO DIRECTO")
  const fp = fp1.concat(fp2)

  let saldoDD = 0
  for(let ref of fp) {
    const { saldO_ACTUAL, descR_CORTA_RELA } = ref
    let saldoActual = Number(saldO_ACTUAL)
    // console.log(saldO_ACTUAL, aRecibir)
    if (saldoActual <= aRecibir &&  descR_CORTA_RELA !== 'HIPOTECA') {
      aRecibir -= saldoActual
      ref.montoAplicado = saldoActual
      ref.montoARecibir = aRecibir
      if(ref.descR_FORMA_PAGO === "DESCUENTO DIRECTO")
        saldoDD += Number(ref.saldO_ACTUAL)
    }
    // if (['PREST. PERSONAL','PREST. AUTO','TARJ. CREDITO'].includes(ref.tipoObligacion)) {
    //   saldoDD += Number(ref.saldoActual)
    // }
  }

  // for(let ref of fp2) {
  //   const { saldoActual, tipoObligacion } = ref
  //   if ( saldoActual <= aRecibir &&  tipoObligacion !== 'HIPOTECA') {
  //     aRecibir -= saldoActual
  //     ref.montoAplicado = saldoActual
  //     ref.montoARecibir = aRecibir
  //     console.log(ref.montoARecibir)
  //   }
  // }
  
  const fpp = fp.filter(f => f.montoAplicado > 0)

  const handleOnClick = () => {

    let contom = 1  // Por el momento se dejan pasar.  RS 2021-10-25
    // let contom = -1
    // for(let ref of fp0) {
    //   const { historia } = ref
    //   if(historia === undefined) {
    //     break
    //   }
      
    //   for(let l of historia) {
    //     if (l > "3") {
    //       contom = 0
    //       break
    //     }
    //   }
    //   if (contom === 0) break
    //   contom = 1
    // }  

    const capDescto = salario * discount_capacity_hip/100
    const capEndeud = salario * (hipoteca_sn ? debt_capacity_hip : debt_capacity)/100

    let letrasP = 0, letrasT = 0
    for(let ref of fp0) {
      if (Number(ref.saldO_ACTUAL) !== ref.montoAplicado) {
        // console.log(ref.saldO_ACTUAL, ref.importE_PAGO)
        letrasP += Number(ref.importE_PAGO)
      }
      letrasT += Number(ref.importE_PAGO)
    }

    const pjeCapEndDespPrest = (letrasP + capDescto)/salario*100
    const pjeCapEndAntePrest = (letrasT)/salario*100
    const endeudamiento = pjeCapEndDespPrest > (hipoteca_sn ? debt_capacity_hip : debt_capacity) ? 0 : 1
    const saldoPorDD = saldoDD > montoLoan ? 0 : 1
    const aplicaPP = contom + endeudamiento + saldoPorDD

    // console.log(letrasP , capDescto, salario)
    // console.log(hipoteca_sn ? debt_capacity_hip : debt_capacity)

    let aplicaAuto = 0, aplicaTC = 0, aplicaHip = 0, capEndDispHip = 0, capEndAoDAuto = 0, capEndAoDTC = 0

    // Calculo capacidad de Endeuda. disponible para prestamo de auto
    if(pjeCapEndDespPrest < pjeEndAuto) capEndAoDAuto = pjeEndAuto - pjeCapEndDespPrest
    else if(pjeCapEndAntePrest < pjeEndAuto) capEndAoDAuto = pjeEndAuto - pjeCapEndAntePrest

    let capEndDispAuto = capEndAoDAuto*salario/100
    if(capEndDispAuto > pjeCapPagoAuto*salario/100) capEndDispAuto = pjeCapPagoAuto*salario/100
    if(capEndDispAuto >= letraMinAuto) aplicaAuto = 1
    aplicaAuto = 1
    //console.log(pjeCapEndDespPrest, pjeCapEndAntePrest, pjeEndAuto)

    // Calculo capacidad de Endeuda. disponible para TC
    if(pjeCapEndDespPrest < pjeEndTC) capEndAoDTC = pjeEndTC - pjeCapEndDespPrest
    else if(pjeCapEndAntePrest < pjeEndTC) capEndAoDTC = pjeEndTC - pjeCapEndAntePrest

    let capEndDispTC = capEndAoDTC*salario/100
    // if(score >= 57 && capEndDispTC > 0) aplicaTC = 1
    if(capEndDispTC > 0) aplicaTC = 1

    const prestAuto = calcMontoAuto()

    const data = {
      '00- Monto Prstamo': montoLoan,
      '01- Monto a Recibir': aRecibir,
      '02- % Capacidad Descto. Hip': discount_capacity_hip,
      '03- % Capacidad Endeudam. Hip': debt_capacity_hip,
      '04- % Capacidad Endeudam.': debt_capacity, 
      '05- Salario': salario,
      '06- Capacidad Descto.': capDescto, 
      '07- Capacidad Endeudam.': capEndeud, 
      '08- Letra Mensual': letrasP, 
      '09- Tiene Hip.': hipoteca_sn,
      '10-': "=============================" ,
      '11- Contometro': contom, 
      '12- Endeudamiento': endeudamiento,
      '13- Saldos por DD': saldoPorDD,
      '14-': "=============================",
      '15- % Cap. Endeud. despues Prest.': pjeCapEndDespPrest,
      '15a- Monto de Letras Pend.': letrasP,
      '15b- Capc. de Descuento': capDescto,
      '16- % Cap. Endeud. antes Prest.': pjeCapEndAntePrest,
      '16a- Total Letra Mensual': letrasT,
      '17- Saldos Descto. Directo': saldoDD,
      '17.-': "=============================",
      '18- % End. TC': pjeEndTC,
      '19- % End. Auto': pjeEndAuto,
      '20- % Cap. Pago Auto': pjeCapPagoAuto,
      '21- % End. Hip': pjeEndHip,
      '21.-': "=============================",
      '22- Aplica para PP': aplicaPP,
      '23- Aplica para AUTO': aplicaAuto,
      '24- Aplica para TC': aplicaTC,
      '25- Aplica para HIP': aplicaHip,
      '26.-': "=============================",      
      '27- Endeudamiento Dispo Auto': capEndDispAuto,
      '28- Endeudamiento Dispo TC': capEndDispTC,
      '29- Endeudamiento Dispo HIP': capEndDispHip,
      '30- Score': score,
      '31-': "=============================",    
      '32- Prest.Auto': prestAuto,
    }
    console.log(data);

    function calcMontoAuto() {
      const fCotiza = new Date()
      let anioHoy = fCotiza.getFullYear(), mesHoy = fCotiza.getMonth()+1 , diaHoy = fCotiza.getDate()
      // console.log(anioHoy, mesHoy, diaHoy)
      for(let i=1; i<85; i++){
        if(mesHoy === 12) {
          mesHoy = 0
          anioHoy++
        }
        mesHoy++
      }
      mesHoy--

      let fVencto = new Date( anioHoy, mesHoy, diaHoy)
      const daysLoan = ((fVencto.getTime() - fCotiza.getTime())/(24*60*60*1000)) + 1
      const promDiasxMes = daysLoan/plazo
      const pagos_x_anio = 360/promDiasxMes
      const tasaMes =  (tasa+1)/pagos_x_anio/100

      const valorAuto = Math.round(((capEndDispAuto/tasaMes*(1-(1/(1+tasaMes)**(plazo)))-total_gastos)/1-0.20)*100)/100

      // console.log(anioHoy, mesHoy, diaHoy)
      // console.log(fCotiza, fVencto, capEndDispAuto, daysLoan, promDiasxMes, pagos_x_anio, tasaMes, valorAuto)
      return {
        fCotiza,
        fVencto,
        daysLoan, 
        promDiasxMes, 
        pagos_x_anio, 
        tasaMes, 
        valorAuto
      }
    }

    setApplication({
      borrower: {
        contometro: contom, 
        endeudamiento,
        saldoPorDD,
        pjeCapEndeud: pjeCapEndDespPrest,
        saldoDD,
        aplicaPP,
        aplicaAuto,
        aplicaTC,
        aplicaHip,
        prestAuto: prestAuto.valorAuto,
        prestTC: 0,
        prestHip: 0,
        scoreAPC: score,
        cashOnHand: aRecibir
      }
    })

    const xx = document.getElementById('aplicar');
    if (xx)
      xx.setAttribute('style', 'display: none')
  }

  const styleButton = {
    padding: '10px 15px 10px 15px',
    border: 'none',
    borderRadius: '25px',
    background: '#0C7BDD',
    color: 'white'
  }

  const handleChange1 = (event: any) => {
    setApplication({
      borrower: {
        aplicaTCOk: event.target.checked
      }
    })
  }
  const handleChange2 = (event: any) => {
    setApplication({
      borrower: {
        aplicaAutoOk: event.target.checked
      }
    })
  }
  const handleChange3 = (event: any) => {
    setApplication({
      borrower: {
        aplicaHipOk: event.target.checked
      }
    })
  }

  const otrosProductos = () => {
    return (
      <>
        {borrower.contometro ? (
          <>
          <br />
          <Typography variant="h6">Otras opciones a las que podria Precalificar</Typography>

            <Table className={classes.table} size="small" aria-label="a dense table" style={{border: '3px solid gray', width: '50%'}}>
              <TableHead>
                <TableRow>
                  <TableCell align="center" style={{fontSize: '20px'}}>Lmite/Monto Mx.</TableCell>
                  <TableCell align="center" style={{borderLeft: '3px solid gray', fontSize: '20px'}}>Opciones</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {(borrower.aplicaTC || 0) === 1 ? (
                  <TableRow>
                    <TableCell>
                      <IconButton color="primary" className={classes.margin}>
                        <CreditCardIcon fontSize="large"/>
                        <Typography>&nbsp;&nbsp;Usted Aplica</Typography>
                      </IconButton>
                    </TableCell>
                    <TableCell align='center' style={{borderLeft: '3px solid gray'}} component="th" scope="row">
                    <Checkbox
                      checked={borrower.aplicaTCOk}
                      onChange={handleChange1}                    
                      color="default"
                      name="aplicaTC"
                      inputProps={{ 'aria-label': 'checkbox with default color' }}
                    />
                    </TableCell>
                  </TableRow>
                ):null}
                {(borrower.aplicaAuto || 0) === 1 ? (
                  <TableRow>
                    <TableCell>
                      <IconButton color="primary" className={classes.margin}>
                        <DriveEtaIcon fontSize="large"/>
                        <Typography>&nbsp;&nbsp;{new Intl.NumberFormat("en-US", {currency: 'USD', minimumFractionDigits: 2}).format(Number(borrower.prestAuto))}</Typography>
                      </IconButton>
                    </TableCell>
                    <TableCell align='center' style={{borderLeft: '3px solid gray'}} component="th" scope="row">
                    <Checkbox
                      checked={borrower.aplicaAutoOk}
                      onChange={handleChange2}
                      color="default"
                      name="aplicaAuto"
                      inputProps={{ 'aria-label': 'checkbox with default color' }}
                    />
                    </TableCell>
                  </TableRow>
                  ):null}
                  {(borrower.aplicaHip || 0) === 1 ? (
                  <TableRow>
                    <TableCell>
                      <IconButton color="primary" className={classes.margin}>
                        <HouseIcon fontSize="large"/>
                        <Typography>&nbsp;&nbsp;$67,000</Typography>
                      </IconButton>
                    </TableCell>
                    <TableCell align='center' style={{borderLeft: '3px solid gray'}} component="th" scope="row">
                    <Checkbox
                      checked={borrower.aplicaHipOk}
                      onChange={handleChange3}                    
                      color="default"
                      name="aplicaHip"
                      inputProps={{ 'aria-label': 'checkbox with default color' }}
                    />
                    </TableCell>                     
                  </TableRow>
                  ):null}
              </TableBody>
            </Table>
          </>
        ):null}
      </>
    )
  }

  return (
    <>
      <Typography variant="h4" align="center">Monto Solicitado: {new Intl.NumberFormat("en-US", {style: 'currency', currency: 'USD', minimumFractionDigits: 2}).format(montoLoan)}</Typography>
      <Typography color={onLine ? 'primary': 'secondary'} variant="h6">Obligaciones Activas Actuales</Typography>

      <HtmlToPdf {...props} />

      <TableContainer component={Paper}>
        <Table className={classes.table} size="small" aria-label="a dense table" style={{border: '3px solid gray'}}>
          <TableHead>
            <TableRow>
              <TableCell>Institucin</TableCell>
              <TableCell>Obligacin</TableCell>
              <TableCell>F.Pago</TableCell>
              <TableCell align="right">Saldo</TableCell>
              <TableCell align="right">Letra</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {fp.map((row) => (
              <TableRow key={row.nuM_REFER}>
                <TableCell component="th" scope="row">
                  {row.noM_ASOC === undefined ?row.message: row.noM_ASOC}
                </TableCell>
                <TableCell>{row.descR_CORTA_RELA}</TableCell>
                <TableCell>{row.descR_FORMA_PAGO}</TableCell>
                <TableCell align="right">{new Intl.NumberFormat("en-US", {currency: 'USD', minimumFractionDigits: 2}).format(Number(row.saldO_ACTUAL === undefined ? 0: row.saldO_ACTUAL))}</TableCell>
                <TableCell align="right">{new Intl.NumberFormat("en-US", {currency: 'USD', minimumFractionDigits: 2}).format(Number(row.importE_PAGO === undefined ? 0: row.importE_PAGO))}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <div id="aplicar">
      <br />
      <button style={styleButton} onClick={handleOnClick}>Aplicar pago Obligaciones</button>
      <br />
      </div>
      {borrower.contometro !== undefined ? (
        <>
          {(borrower.aplicaPP === undefined? 0: borrower.aplicaPP < 3) ? (
            <>
              <br />
              <Typography variant="h6">Lamentablemente usted no califica debido a los siguientes criterios</Typography>

              <TableContainer>
                  <Table className={classes.table} size="small" aria-label="a dense table" style={{border: '3px solid gray', width: '80%'}}>
                    <TableHead>
                      <TableRow>
                        <TableCell align="center" style={{fontSize: '20px'}}>Criterio</TableCell>
                        <TableCell align="center" style={{borderLeft: '3px solid gray', fontSize: '20px'}}>Comentario</TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {!borrower.contometro ? (
                        <TableRow>
                          <TableCell>
                            <IconButton color="secondary" className={classes.margin}>
                              <CalendarTodayIcon fontSize="large"/>
                              <Typography>&nbsp;&nbsp;Das de Atraso</Typography>
                            </IconButton>
                          </TableCell>
                          <TableCell style={{borderLeft: '3px solid gray'}} component="th" scope="row">En 24 meses a mantenido atrasos &gt; 60 das</TableCell>
                        </TableRow>
                      ):null}
                      {!borrower.saldoPorDD ? (
                        <TableRow>
                          <TableCell>
                            <IconButton color="secondary" className={classes.margin}>
                              <EventBusyIcon fontSize="large"/>
                              <Typography>&nbsp;&nbsp;Nivel Endeudamiento</Typography>
                            </IconButton>
                          </TableCell>
                          <TableCell style={{borderLeft: '3px solid gray'}} component="th" scope="row">45% ( se excede de los requerido )</TableCell>
                        </TableRow>
                        ):null}
                        {!borrower.endeudamiento ? (
                        <TableRow>
                          <TableCell>
                            <IconButton color="secondary" className={classes.margin}>
                              <LocalAtmIcon fontSize="large"/>
                              <Typography>&nbsp;&nbsp;Total Obligaciones por Descuento</Typography>
                            </IconButton>
                          </TableCell>
                          <TableCell style={{borderLeft: '3px solid gray'}} component="th" scope="row">Se excede de lo solicitado</TableCell>                     
                        </TableRow>
                        ):null}
                        {(borrower.scoreAPC || 0) < 57 ? (
                        <TableRow>
                          <TableCell>
                            <IconButton color="secondary" className={classes.margin}>
                              <SportsHandballIcon fontSize="large"/>
                              <Typography>&nbsp;&nbsp;Score APC</Typography>
                            </IconButton>
                          </TableCell>
                          <TableCell style={{borderLeft: '3px solid gray'}} component="th" scope="row">{borrower.scoreAPC}%</TableCell>
                        </TableRow>
                        ):null}
                    </TableBody>
                  </Table>
                </TableContainer>   

                {otrosProductos()}
            </>
          ) : 
          (
            <>
              {fpp.length ? (
                <>
                  <br />
                  <Typography variant="h6">Obligaciones a Cancelar por Descuento</Typography>

                  <TableContainer>
                    <Table className={classes.table} size="small" aria-label="a dense table" style={{border: '3px solid gray', width: '80%'}}>
                      <TableHead>
                        <TableRow>
                          <TableCell>Institucin</TableCell>
                          <TableCell>Obligacin</TableCell>
                          <TableCell align="right">Saldo</TableCell>
                          <TableCell align="right">Letra</TableCell>
                        </TableRow>
                      </TableHead>
                      <TableBody>
                        {fpp.map((row) => (
                          <TableRow key={row.nuM_REFER}>
                            <TableCell component="th" scope="row">
                              {row.noM_ASOC}
                            </TableCell>
                            <TableCell>{row.descR_CORTA_RELA}</TableCell>
                            <TableCell align="right">{new Intl.NumberFormat("en-US", {currency: 'USD', minimumFractionDigits: 2}).format(Number(row.saldO_ACTUAL))}</TableCell>
                            <TableCell align="right">{new Intl.NumberFormat("en-US", {currency: 'USD', minimumFractionDigits: 2}).format(Number(row.importE_PAGO === undefined ? 0: row.importE_PAGO))}</TableCell>
                          </TableRow>
                        ))}
                      </TableBody>
                    </Table>
                  </TableContainer>   
                  <Typography variant="h6">con el cual, usted recibir en mano despues de dichas cancelaciones</Typography>

                  <Typography variant="h4">Monto a Recibir: {new Intl.NumberFormat("en-US", {style: 'currency', currency: 'USD', minimumFractionDigits: 2}).format(aRecibir)}</Typography>
                  {otrosProductos()}
                </>
              ):""}
            </>
          )}
        </>
      )
      :""}
    </>
  );
}

export default Form;