import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';

import {
  STEPS,
  Application
} from "../types";
import Forms from '../Forms';


interface Stepper {
  next?: () => void;
  prev?: () => void;
  disabled: boolean;
  Form: React.ReactElement;
  step: STEPS
};

function useStep(
  application: Application, 
  setApplication: (application: Partial<Application>) => void
): Stepper {
  let history = useHistory();
  const [step, setStep] = useState(STEPS.BORROWER);

  const finishQuote = async (causas = "") => {
  
    //***********************************************************//
    //***********************************************************//
    // Buscar Token de Clientify
    //***********************************************************//
    //***********************************************************//
    let Token = null
    let Clientify = window.localStorage.getItem('Clientify') || ''
    if (Clientify) {
      Token = JSON.parse(Clientify).token
    }

    if (!Token) {
      const response: Response = await fetch("/api/clientify-token", {
        method: 'POST'
      })
  
      // Guardamos Token de Clientify
      const data = await response.json();
      window.localStorage.setItem("Clientify",  JSON.stringify(data))
    }

    let IDClientify = window.localStorage.getItem('IDClientify') || ''
    
    // Guardamos la causa del Rechazo
    var raw = {
      "token": Token,
      "ID": IDClientify,
      "Tracking": "RECHAZADO POR: " + causas.toUpperCase(),
    };

    let method = "POST"
    if(IDClientify) method = "PUT"

    const response = await fetch("/api/clientify-rechazo", {
      method: method,
      body:  new URLSearchParams(raw)
    })

    const result = await response.json();
    // Guardamos id del contacto en Clientify
    window.localStorage.setItem("IDClientify",  JSON.stringify(result.id))

    history.push('/cotizar/finalizar');
  }

  const API_HOST = ''; //process.env.REACT_APP_API_HOST;
  const aproach = () => history.push('/seguimiento');

  const props = {
    application, 
    setApplication
  };

  //***************************************//
  //***************************************//
  //** Guarda Informacion del Prospecto  **//
  //***************************************//
  //***************************************//
  const saveProspect = async () => {
    const { application: {
      borrower, personalReferenceRelated, personalReferenceUnrelated, health1, health2, health3, documents, sign 
    }} = props;

    let body = {}
    const dataApp = { borrower, personalReferenceRelated, personalReferenceUnrelated, health1, health2, health3, documents };

    if (dataApp) {
      // const dataAppObj = JSON.parse(dataApp)
      let { id, maritalStatus, residentialNumber, province, district, prestAuto, prestHip, prestTC, cashOnHand } = borrower
      const { idUrl, socialSecurityProofUrl, publicGoodProofUrl, workLetterUrl, payStubUrl } = documents
      // console.log(id, maritalStatus, phoneNumber, residentialNumber, wage)

      body = {
        id_personal: id,
        phoneNumber: residentialNumber,
        civil_status: maritalStatus,
        idUrl, socialSecurityProofUrl, publicGoodProofUrl, workLetterUrl, payStubUrl,
        province,
        district,
        application: JSON.stringify(dataApp),
        sign: sign.signImage || '',
        loanAuto: prestAuto,
        loanTC: prestTC,
        loanHip: prestHip,
        cashOnHand
      }
    }

    let email2 = '', telefono = '', monto = '', name = '', banco = ''

    const dataQtt = window.localStorage.getItem('quotation');
  if (dataQtt) {
      const dataAttObj = JSON.parse(dataQtt)
      const {birthDate, contractType, gender, jobSector, occupation = 0, paymentFrecuency, profession = 0, residenceType, wage} = dataAttObj.borrower

      const {bank, amount = 0, term = 0} = dataAttObj.loan
      const {email, fname, fname_2 = '', lname, lname_2 = '', origin = '', idUser = '', cellphone = ''} = dataAttObj.user

      monto = amount
      telefono = cellphone
      email2 = email
      banco = bank

      name = fname + ' '
      if(fname_2) name += fname_2 + ' '
      name += lname + ' ' + lname_2

      body = {...body, estado: 1, email, name, lname, lname_2, fname, fname_2, origin_idUser: origin, entity_f: bank, gender, birthDate, contractType, 
        jobSector: (jobSector === 'P') ? 1 : ((jobSector === 'Pb') ? 2 : 3), 
        occupation, paymentFrecuency, profession, residenceType, salary: wage, idUser, cellphone, 
        loanPP: amount, plazo: term, 
      quotation: dataQtt
      }
    }


    try {

      debugger
      fetch(`${API_HOST}/adm/prospects`, {
        method: 'POST',
        body: new URLSearchParams(body)
      })

      body = {
        email: email2,
        asunto: "Solicitud de Prstamo de: >> " + name,
        mensaje: "Solicitud de Prstamo desde www.Finanservs.com",
        telefono: telefono,
        monto: monto,
        nombre: name,
        banco: banco,
      }

      fetch(`${API_HOST}/api/email`, {
        method: 'POST',
        body: new URLSearchParams(body)
      })
    } catch (err) {
      console.log('Error: (Prospect)',err);
    }

    return
  }

  //***************************************//
  //***************************************//
  //** Guarda Informacion del Tracking  **//
  //***************************************//
  //***************************************//
  const saveTracking = async (tracking = "") => {

    // const { application: tracking = "" } = props;

    const { application: { borrower: { 
      id = "",
      maritalStatus = "",
      province = "0",
      district = "0",
      county = "0",
      street = "N/A",
      residentialNumber = "",
      company = "N/A",
      position = "N/A",
      companyAddress = "",
      companyPhoneNumber = "",
      companyPhoneExtension = "",
      previousCompany = ""
    }}} = props;

    const { application: { documents: { 
      idUrl = "N/A",
      socialSecurityProofUrl = "N/A",
      publicGoodProofUrl = "N/A",
      workLetterUrl = "N/A",
      payStubUrl = "N/A",
      apcLetterUrl = "N/A",
      apcReferencesUrl = "N/A",
    }}} = props;

    const { application: { personalReferenceRelated: { 
      name: nameRefP,
      lastName: lastNameRefP,
      relationship: relationshipRefP,
      phoneNumber: phoneNumberRefP,
      residenceNumber: residenceNumberRefP,
      company: companyRefP,
      companyPhoneNumber: companyPhoneNumberRefP,
      companyPhoneExtension: companyPhoneExtensionRefP,
    }}} = props;

    const { application: { personalReferenceUnrelated: { 
      name: nameRefNP,
      lastName: lastNameRefNP,
      relationship: relationshipRefNP,
      phoneNumber: phoneNumberRefNP,
      residenceNumber: residenceNumberRefNP,
      company: companyRefNP,
      companyPhoneNumber: companyPhoneNumberRefNP,
      companyPhoneExtension: companyPhoneExtensionRefNP,
    }}} = props;

    const dataQtt = window.localStorage.getItem('quotation');
    if (dataQtt) {
      const dataAttObj = JSON.parse(dataQtt)
      const { birthDate, gender, jobSector, occupation = '', institution = 'N/A', 
        profession = '', 
        retirement = '',
        contractType,
        currentJobMonths = '0',
        previousJobMonths = '0',
        creditHistory,
        paymentFrecuency,
        wage,
        alloance = '0',
        perDiem = '0',
        residenceType,
        monthlyResidenceFee = '0',
      } = dataAttObj.borrower

      let birth = new Date()
      if (birthDate !== undefined) {
        birth = new Date(birthDate)
      }

      const {email, fname = "", fname_2, lname, lname_2, cellphone, nationality, terms_cond = false} = dataAttObj.user
      const { bank = "0", opciones = "" } = dataAttObj.loan;  

      let body = {
        id_param: "",
        Nombre: fname,
        Segundo_Nombre: fname_2,
        Apellido_Paterno: lname,
        Apellido_Materno: lname_2,
        Email: email,
        Celular: cellphone,
        Telefono_Casa: residentialNumber,
        Genero: gender,
        Nacionalidad: nationality,
        Fecha_Nac: birth.toString(),
        Terminos_Condiciones: terms_cond ? "Si" : "No",

        Sector: jobSector,
        Profesion: profession,
        Institucion: institution,
        Ocupacion: occupation,
        Planilla_CSS: retirement,
        Tipo_Residencia: residenceType,
        Mensualidad: monthlyResidenceFee,
        Historial_Credito: creditHistory,
        Frecuencia_Pago: paymentFrecuency,

        Tracking: tracking.toString(),
        "Modificado el": new Date().toString(),
        Numero_Id: id,
        Estado_Civil: maritalStatus,
        Provincia: province.toString(),
        Distrito: district.toString(),
        Corregimiento: county.toString(),
        Calle_No: street,

        Compania_Trabajo: company,
        Cargo: position,
        Tipo_Contrato: contractType,
        Direccion_Trabajo: companyAddress,
        Telefono_Trabajo: companyPhoneNumber,
        Extension_Trabajo: companyPhoneExtension,
        Meses_Trabajo_Actual: currentJobMonths,
        Trabajo_Anterior: previousCompany,
        Meses_Trabajo_Anterior: previousJobMonths,

        Salario: wage,
        Servicios_Profesionales: alloance,
        Viaticos: perDiem,

        Entidad_Seleccionada: bank,
        Prestamo_Opciones: JSON.stringify(opciones),

        Img_ID: idUrl,
        Img_Ficha_CSS: socialSecurityProofUrl,
        Img_Servicio_Publico: publicGoodProofUrl,
        Img_Carta_Trabajo: workLetterUrl,
        Img_Comprobante_Pago: payStubUrl,
        Img_Autoriza_APC: apcLetterUrl,
        Img_Referencias_APC: apcReferencesUrl,

        Ref_Familia_Nombre: (nameRefP || '').toString(),
        Ref_Familia_Apellido: (lastNameRefP || '').toString(),
        Ref_Familia_Parentesco: (relationshipRefP || '').toString(),
        Ref_Familia_Telefono: (phoneNumberRefP || '').toString(),
        Ref_Familia_Casa_No: (residenceNumberRefP || '').toString(),
        Ref_Familia_Empresa: (companyRefP || '').toString(),
        Ref_Familia_Empresa_Telefono: (companyPhoneNumberRefP || '').toString(),
        Ref_Familia_Empresa_Extension: (companyPhoneExtensionRefP || '').toString(),

        Ref_No_Familia_Nombre: (nameRefNP || '').toString(),
        Ref_No_Familia_Apellido: (lastNameRefNP || '').toString(),
        Ref_No_Familia_Parentesco: (relationshipRefNP || '').toString(),
        Ref_No_Familia_Telefono: (phoneNumberRefNP || '').toString(),
        Ref_No_Familia_Casa_No: (residenceNumberRefNP || '').toString(),
        Ref_No_Familia_Empresa: (companyRefNP || '').toString(),
        Ref_No_Familia_Empresa_Telefono: (companyPhoneNumberRefNP || '').toString(),
        Ref_No_Familia_Empresa_Extension: (companyPhoneExtensionRefNP || '').toString()
      }
      
      let ID = null
      let response = await fetch(`${API_HOST}/api/tracking/${email}`, {
        method: "GET"
      })
      let result = await response.json();

      let method = "POST"
      if(result.length > 0) {
        ID = result[0]._id
        method = "PUT"
        body.id_param = ID
      }

      let res: Response = await fetch(`${API_HOST}/api/tracking`, {
        method: `${method}`,
        body: new URLSearchParams(body),
      })
      
      // METERLE UN TRY CATCH
      result = await res.json();
      console.log("Method: ===============> ", method, ID)
      if(method === "POST") {
        window.localStorage.setItem("IDMongo",  JSON.stringify(result))
      } else {
        window.localStorage.setItem("IDMongo",  JSON.stringify({ "ID": ID }))
      }


      //***********************************************************//
      //***********************************************************//
      // Buscar Token de Clientify
      //***********************************************************//
      //***********************************************************//
      let Token = null
      let Clientify = window.localStorage.getItem('Clientify') || ''
      if (Clientify) {
        Token = JSON.parse(Clientify).token
      }

      let IDClientify = window.localStorage.getItem('IDClientify') || ''
      console.log('IDClientify', IDClientify)

      var raw = {
        "token": Token,
        "ID": IDClientify,
        "Tracking": tracking.toUpperCase(),
        "tipo_residencia": (residenceType || '').toString(),
        "mensualidad_casa": monthlyResidenceFee.toString(),

        "Cedula": id,
        "donde_trabaja": company,
        "Puesto": position,

        province,
        district,
        county,
        street,

        "img_cedula": idUrl,
        "img_ficha_css": socialSecurityProofUrl,
        "img_servicio_publico": publicGoodProofUrl,
        "img_carta_trabajo": workLetterUrl,
        "img_comprobante_pago": payStubUrl,
        "img_autoriza_apc": apcLetterUrl,
        "img_referencias_apc": apcReferencesUrl,

        "first_name": fname,
        "last_name": lname,
        "email": email,
        "phone": cellphone,
        "fecha_nacimiento": birth.toString(),
        "acepta_terminos_condiciones": terms_cond ? "Si" : "No",
        "contrato_laboral": (contractType || '0') === '0' ? 'Temporal' : (contractType || '0') === '1' ? 'Permanente' : 'Serv. Profesional',
        "meses_trabajo_actual": currentJobMonths.toString(),
        "meses_trabajo_anterior": previousJobMonths.toString(),
        "Salario": wage.toString(),
        "Sector": jobSector === 'Pb' ? 'Pblico': jobSector === 'P' ? 'Privado' : 'Jubilado',
        "Institucion": institution,
        "Ocupacion": occupation,
        "Planilla": retirement,
        "Profesion": profession,
        "Genero": gender === 'male' ? "Hombre": "Mujer",

        "entidad_seleccionada": bank.toString(),
        "prestamo_opciones": JSON.stringify(opciones),
      };

      response = await fetch("/api/clientify", {
        method: 'PUT',
        body:  new URLSearchParams(raw)
      })

      // METERLE UN TRY CATCH
      result = await response.json();

    }
    return
  }

  // Movido a la parte de arriba
  // const props = {
  //   application, 
  //   setApplication
  // };

  useEffect(() => {
    const dataApp = window.localStorage.getItem('application');
    if (dataApp) {
      setApplication(JSON.parse(dataApp))
      setApplication({
        borrower: {contometro: undefined}
      })
    }
  }, [])

  return ({
    [STEPS.BORROWER]: () => ({
      step,
      next: () => {
        setApplication({ tracking: "Cedula y Estado Civil" })
        saveTracking("Cedula y Estado Civil")
        window.localStorage.setItem('application', JSON.stringify(application));
        window.localStorage.setItem('finanservsIdUser', application.borrower.id || '')

        console.log(application)
        setStep(STEPS.DOCUMENTS)
      },
      disabled: (() => {
        //, phoneNumber
        const { id, maritalStatus } = application.borrower;
        return  (!id || !maritalStatus);        
      })(),      
      Form: <Forms.Borrower {...props} />
    }),
    [STEPS.DOCUMENTS]: () => ({
      step,
      next: () => {
        setApplication({ tracking: "Imagenes de Documentos" })
        saveTracking("Imagenes de Documentos")
        window.localStorage.setItem('application', JSON.stringify(application));
        setStep(STEPS.ADDRESS)
      },
      prev: () => setStep(STEPS.BORROWER),
      disabled: (() => {
        const { idUrl, payStubUrl, publicGoodProofUrl, socialSecurityProofUrl, workLetterUrl } = application.documents;
        return  (!idUrl || !payStubUrl || !publicGoodProofUrl || !socialSecurityProofUrl || !workLetterUrl);        
      })(), 
      Form: <Forms.Documents {...props} />
    }),    
    [STEPS.ADDRESS]: () => ({
      step,
      next: () => {
        setApplication({ tracking: "Direccin" })
        saveTracking("Direccin")
        window.localStorage.setItem('application', JSON.stringify(application));
        setStep(STEPS.WORK)
      },
      prev: () => setStep(STEPS.DOCUMENTS),
      disabled: (() => {
        const { county, district, province, residentialNumber, street } = application.borrower;
        return  (!county || !district || !province || !residentialNumber || !street);        
      })(), 
      Form: <Forms.Address {...props} />
    }),
    [STEPS.WORK]: () => ({
      step,
      next: () => {
        setApplication({ tracking: "Info del Trabajo" })
        saveTracking("Info del Trabajo")
        window.localStorage.setItem('application', JSON.stringify(application));
        setStep(STEPS.PERSONAL_REFERENCE_RELATED)
      },
      prev: () => setStep(STEPS.ADDRESS),
      disabled: (() => {
        const { company, position, companyAddress, companyPhoneNumber } = application.borrower;
        // const { company, position, companyAddress, companyPhoneNumber, previousCompany, wage } = application.borrower;
        return  (!company || !position || !companyAddress ||!companyPhoneNumber);        
        // return  (!company || !position || !companyAddress ||!companyPhoneNumber || !previousCompany || !wage);        
      })(), 
      Form: <Forms.Work {...props} />
    }),
    [STEPS.PERSONAL_REFERENCE_RELATED]: () => ({
      step,
      next: () => {
        setApplication({ tracking: "Ref. Personales Familia" })
        saveTracking("Ref. Personales Familia")
        window.localStorage.setItem('application', JSON.stringify(application));
        setStep(STEPS.PERSONAL_REFERENCE_UNRELATED)
      },
      prev: () => setStep(STEPS.WORK),
      disabled: (() => {
        const { name, lastName, relationship, residenceNumber, phoneNumber, company, companyPhoneNumber } = application.personalReferenceRelated;
        return  (!name || !lastName || !relationship || !residenceNumber || !phoneNumber || !company || !companyPhoneNumber);        
      })(), 
      Form: <Forms.PersonalReferenceRelated {...props} />
    }),
    [STEPS.PERSONAL_REFERENCE_UNRELATED]: () => ({
      step,
      next: () => {
        setApplication({ tracking: "Ref. Personales no Familia" })
        saveTracking("Ref. Personales no Familia")
        window.localStorage.setItem('application', JSON.stringify(application));
        setStep(STEPS.HEALTH_1)
      },
      prev: () => setStep(STEPS.PERSONAL_REFERENCE_RELATED),
      disabled: (() => {
        const { name, lastName, residenceNumber, phoneNumber, company, companyPhoneNumber } = application.personalReferenceUnrelated;
        return  (!name || !lastName || !residenceNumber || !phoneNumber || !company || !companyPhoneNumber);        
      })(), 
      Form: <Forms.PersonalReferenceUnrelated {...props} />
    }),
    [STEPS.HEALTH_1]: () => ({
      step,
      next: () => {
        window.localStorage.setItem('application', JSON.stringify(application));
        setStep(STEPS.HEALTH_2)
      },
      prev: () => setStep(STEPS.PERSONAL_REFERENCE_UNRELATED),
      disabled: (() => {
        const { doctor, doctorSpecialty, healthCenter } = application.health1;
        return  (!doctor || !doctorSpecialty || !healthCenter);        
      })(), 
      Form: <Forms.Health1 {...props} />
    }),
    [STEPS.HEALTH_2]: () => ({
      step,
      next: () => {
        window.localStorage.setItem('application', JSON.stringify(application));
        setStep(STEPS.HEALTH_3)
      },
      prev: () => setStep(STEPS.HEALTH_1),
      disabled: (() => {
        const { question1, question2, question3, question4, question5, question6, question7, question8 } = application.health2;
        return  (question1===undefined || question2===undefined || question3===undefined || question4===undefined || question5===undefined || question6===undefined || question7===undefined || question8===undefined);        
      })(),
      Form: <Forms.Health2 {...props} />
    }),
    [STEPS.HEALTH_3]: () => ({
      step,
      next: () => {
        window.localStorage.setItem('application', JSON.stringify(application));
        setStep(STEPS.SIGNED)
      },
      prev: () => setStep(STEPS.HEALTH_2),
      disabled: (() => {
        const gender = window.localStorage.getItem('gender');
        const { question1, question2, question3, question4, question5, question6, question7, question8, question9 } = application.health3;
        return  ((question1===undefined && gender==="female") || question2===undefined || question3===undefined || question4===undefined || question5===undefined || question6===undefined || question7===undefined || question8===undefined || question9===undefined);        
      })(),
      Form: <Forms.Health3 {...props} />
    }),
    [STEPS.SIGNED]: () => ({
      step,
      next: () => {
        // application.sign.signImage = undefined
        setApplication({ tracking: "Autoriza/Firma Referencias de APC" })
        saveTracking("Autoriza/Firma Referencias de APC")
        window.localStorage.setItem('application', JSON.stringify(application));

        const id = application.borrower.id || ""
        const data = window.localStorage.getItem('APC-'+id)

        if(data){
          const expires = new Date(JSON.parse(data).expiresIn)
          if(expires < new Date()) {
            window.localStorage.removeItem('APC-'+id)
            setStep(STEPS.APPROACH)
          } else {
            setStep(STEPS.APPROACH_2)
          }
        } else {
          setStep(STEPS.APPROACH)
        }

      },
      prev: () => setStep(STEPS.HEALTH_3),
      disabled: (() => {
        const { accept } = application.borrower;
        const { signImage } = application.sign;

        let siguiente = true;
        if (accept !== undefined && accept === 1 && signImage !== undefined && signImage !== '' && signImage !== null && signImage !== "data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAYAAAAfFcSJAAAAC0lEQVQYV2NgAAIAAAUAAarVyFEAAAAASUVORK5CYII=") {
          siguiente = false
        }

        return siguiente;
      })(),
      Form: <Forms.Signed {...props} />
    }),
    [STEPS.APPROACH]: () => ({
      step,
      next: () => {
        saveProspect()
        aproach()
        setApplication({ tracking: "Finanlizado!" })
        saveTracking("Finanlizado!")
        window.localStorage.setItem('application', JSON.stringify(application));

        const { contometro=0, endeudamiento=0, saldoPorDD=0, prestAuto=0, prestTC=0, prestHip=0 } = application.borrower;
        
        console.log(contometro, endeudamiento, saldoPorDD, prestAuto, prestTC, prestHip)
        if ((contometro + endeudamiento + saldoPorDD) < 3 && (prestAuto + prestTC + prestHip) <= 0 ) {
          return finishQuote(`Historial Crediticio APC (aplica?): Contometro ${contometro === 1? 'Si': 'No'}, endeudamiento ${endeudamiento === 1? 'Si': 'No'} Saldos por Descto Directo ${saldoPorDD} === 1? 'Si': 'No'`);
        }
      },
      prev: () => setStep(STEPS.SIGNED),
      disabled: (() => {
        const { contometro, endeudamiento, saldoPorDD } = application.borrower;
        return  (contometro===undefined || contometro===-1 || endeudamiento===undefined || saldoPorDD===undefined);  
      })(),
      Form: <Forms.Approach {...props} />
    }),
    [STEPS.APPROACH_2]: () => ({
      step,
      next: () => {
        saveProspect()
        aproach()
        setApplication({ tracking: "Finanlizado!" })
        saveTracking("Finanlizado!")
        window.localStorage.setItem('application', JSON.stringify(application));
        const { contometro=0, endeudamiento=0, saldoPorDD=0, prestAuto=0, prestTC=0, prestHip=0 } = application.borrower;
        
        console.log(contometro, endeudamiento, saldoPorDD, prestAuto, prestTC, prestHip)
        if ((contometro + endeudamiento + saldoPorDD) < 3 && (prestAuto + prestTC + prestHip) <= 0 ) {
          return finishQuote(`Historial Crediticio APC (aplica?): Contometro ${contometro === 1? 'Si': 'No'}, endeudamiento ${endeudamiento === 1? 'Si': 'No'} Saldos por Descto Directo ${saldoPorDD} === 1? 'Si': 'No'`);
        }
      },
      prev: () => setStep(STEPS.SIGNED),
      disabled: (() => {
        const { contometro, endeudamiento, saldoPorDD } = application.borrower;
        return  (contometro===undefined || contometro===-1 || endeudamiento===undefined || saldoPorDD===undefined);  
      })(),
      Form: <Forms.Aproach2 {...props} />
    })
  })[step]();
};

export default useStep;